.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
}

.text-center {
  text-align: center;
}

.img-responsive {
  display: block;
  max-width: 100%;
}

.ant-btn {
  border: none;

  &:hover,
  &:active,
  &:focus {
    color: #f36a22;
  }
}

.link {
  color: black;

  &:hover {
    color: #f36a22;
  }
}

.header {
  &.sticky {
    position: fixed;
    top: 0;
    z-index: 16;
    width: 100%;
    left: 0;
    background: rgba(black, .8);

    .main-menu__link {
      color: white;
    }
  }
}

.menubar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
}

.main-menu {
  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__link {
    padding: 0 24px;
    font-weight: bold;
    text-transform: uppercase;
  }
}

.topbar {
  padding: 12px 0;
  background: #f36a22;

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__contact {
    a {
      color: white;

      &:hover {
        color: white;
      }
    }

    span {
      padding: 0 8px;
    }
  }

  &__cart {
    .link {
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      height: min-content;
    }

    svg {
      padding-right: 8px;
    }
  }
}

.item {
  img {
    width: 100%;
    height: 85vh;
    object-fit: cover;
  }
}

.ft {
  display: flex;
  align-items: baseline;
  padding: 32px 16px;

  &__title {
    position: relative;
    padding: 0px;
    margin: 0px 0px 10px 0px;
    color: #f36a22;
    font-weight: 600;
    font-size: 16px;
    text-transform: uppercase;
  }

  &__contact {
    padding-right: 32px;

    &-icon {
      margin-right: 8px;
    }

    &-desc {
      padding-bottom: 10px;
      margin-bottom: 10px;
      border-bottom: 1px solid #dbdbdd;
    }

    > div {
      margin-bottom: 10px;
      display: flex;
      align-items: center;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__subscribe {
    margin-left: 32px;
  }

  &-contact {
    padding: 16px 0;
  }
}

.pay_acept {
  margin-top: 10px;
  margin-bottom: 30px;
  max-width: 300px;

  img {
    display: block;
    max-width: 100%;
  }
}

section {
  padding: 40px 0;
}

.section-title {
  display: flex;
  align-items: center;
  margin-bottom: 16px;

  h2 {
    display: inline-block;
    font-weight: 900;
    color: #f36a22;
    margin: 0px;
    font-size: 24px;
    text-transform: uppercase;
  }
}

.page-title {
  font-weight: 900;
  color: #f36a22;
  margin: 0px;
  font-size: 32px;
  text-transform: uppercase;
  margin: 16px 0;
}

.ant-card-meta-description {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.ant-form-item-label {
  min-width: 200px;
  text-align: left;
}

.contact-wrapper {
  margin: 32px 0 0 32px;
}

.footer {
  font-size: 14px;
  line-height: 28px;

  a {
    font-size: 14px;
  }
}

.contact {
  padding: 120px 0;
}

.price {
  margin: 16px 0;
  font-weight:600;

  &__number {
    font-size: 16px;
    padding-left: 4px;
    color: #f10000;
  }
}

.product-add__button {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.button {
  button {
    width: calc(50% - 8px);
    background-color: #f36a22;
    border: 1px solid #f36a22;

    &:hover,
    &:focus,
    &:active {
      background-color: #d3530e;
      border: 1px solid #d3530e;
    }
  }

  .button__detail {
    width: calc(50% - 8px);

    button {
      width: 100%;
    }
  }
}

.ant-card {
  margin-bottom: 16px;
  border-radius: 8px;
  cursor: pointer;
  position: relative;
  z-index: 1;

  &-cover {
    position: relative;
    z-index: 1;
    overflow: hidden;
    transition: all .5s;
    cursor: pointer;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;

    img {
      height: 186px;
      object-fit: cover;

      &:hover {
        transform: scale(1.05);
        transition: all .5s;
      }
    }
  }
}

.blog-index {
  background: #ddd;

  .ant-card {
    &-body {
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.6);
      padding: 0;
      z-index: 2;

      .ant-card-meta-title {
        color: #fff;
        padding: 16px;
        font-size: 16px;
      }
    }
  }
}


.image {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 200px;

  &__center {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  svg {
    width: 20px;
    height: 20px;
    color: #fff;
  }
}

.product {
  padding-left: 24px;

  &__title {
    color: #d3530e;
    font-size: 28px;
    margin-bottom: 16px;
    font-weight: bold;
  }

  &__description {
    margin-bottom: 8px;
  }

  &__price {
    color: #ff1111;
    font-weight: 600;
    font-size: 36px;
    margin-bottom: 16px;
  }
}

.detail {
  img {
    width: 100%;
    min-height: 400px;
    object-fit: cover;
  }
}

.tabpanel {
  white-space: pre-line;
}

.ant-tabs-tab {
  color: #f36a22;
}

.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #f36a22;
}

.ant-tabs-ink-bar {
  background: #f36a22;
}

table {
  td,
  th {
    padding: 8px 12px;
    border: 1px solid #ebebeb;

    &:first-child,
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6) {
      text-align: center;
      margin: 0 auto;
      max-width: 10vw;
      width: 8%;
    }

    &:nth-child(2) {
      min-width: 10vw;
    }

    &:nth-child(3) {
      .img-responsive {
        min-width: 200px;
        height: 100px;
        margin: 0 auto;
        object-fit: cover;
      }
    }
  }

  th {
    background: #ebebeb;
    text-align: center;
    text-transform: uppercase;
    font-size: 12px;
  }
}

.table-responsive {
  display: block;
  width: 100%;
  overflow-x: auto;
}

.cart__button {
  margin: 24px 0 0;
  text-align: right;

  &-submit {
    background: #d3530e;
    border-radius: 4px;
    color: #fff;
    text-transform: uppercase;
    line-height: 20px;

    &:hover,
    &:active,
    &:focus {
      color: #fff;
      opacity: .9;
      background: #d3530e;
    }
  }
}

@media (max-width: 990px) {
  .menubar,
  .ft {
    flex-direction: column;
  }

  .main-menu {
    margin-top: 24px;
  }

  .item img {
    height: 74vh;
  }

  .ft {
    &__subscribe {
      margin: 16px 0 0 0;
    }
  }

  .sticky {
    .logo {
      display: none;
    }

    .main-menu {
      margin-top: 0;
    }
  }

  .contact {
    padding: 0;

    &-wrapper {
      display: none;
    }
  }

  .product {
    padding-top: 24px;
    padding-left: 0;
  }

  table {
    td,
    th {
      min-width: 30vw;

      &:first-child,
      &:nth-child(6) {
        min-width: 16vw;
      }

      &:nth-child(2) {
        min-width: 50vw;
      }
    }
  }
}
